.card {
    position: relative;
    cursor: pointer;
}

.card img {
    width: 100%;
    display: block;
    border: 2px solid#fff;
    border-radius: 6px;
}
/* front of card - the picture */
.card .front {
    transform: rotateY(90deg);
    transition: all ease-in .2s;
    position: absolute;
}
.flipped .front {
    transform: rotateY(0deg);
    transition-delay: .2s;
}

/* back of card - cover */
.card .back {
    transition: all ease-in .2s;
    transition-delay: .2s;
}
.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}